body{
    background-color: rgb(0, 0, 0);
}
.one{
    background-color: rgba(0, 162, 255, 0);
    border-radius: 10px 10px;
    overflow: hidden;
}
.aniaimg{
    animation-name: seat;
    animation-duration: 40s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    overflow: hidden;
    width: 100%;
    transform: translateX(180px)
}
@keyframes seat {
  0%{
    transform: translateX(300px) scale(1.8);
    
  }
  25%{
    transform: translateX(50px) scale(1.1);
    
  }
  50%{
    transform: translateX(-300px) scale(1.8);
    
  }
  75%{
    transform: translateX(50px) scale(1.8);
    
  }
  100%{
    transform: translateX(300px) scale(1.8);
    
  }
  
}
.aniaimg:hover{
    background-color: rgba(255, 0, 0, 0.288);
}
@media screen and (max-width: 700px){
  .aniaimg{
    animation-name: seatmin;
    animation-duration: 40s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    overflow: hidden;
    width: 100%;
    transform: translateX(180px)
}
}
@keyframes seatmin {
  0%{
    transform: translateX(250px) scale(2.1);
    
  }
  25%{
    transform: translateX(50px) scale(1.5);
    
  }
  50%{
    transform: translateX(-250px) scale(2.1);
    
  }
  75%{
    transform: translateX(50px) scale(1.5);
    
  }
  100%{
    transform: translateX(250px) scale(2.1);
    
  }
  
}
@media screen and (max-width: 400px){
  .aniaimg{
    animation-name: seatmob;
    animation-duration: 40s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    overflow: hidden;
    width: 100%;
    transform: translateX(180px)
}
}
@keyframes seatmob {
  0%{
    transform: translateX(200px) scale(2.2);
    
  }
  25%{
    transform: translateX(50px) scale(1.7);
    
  }
  50%{
    transform: translateX(-200px) scale(2.2);
    
  }
  75%{
    transform: translateX(50px) scale(1.7);
    
  }
  100%{
    transform: translateX(200px) scale(2.2);
    
  }
  
}
.biobox{
  background-color: rgba(0, 0, 0, 0.308);
  color: #fff;
  padding: 30px;
  border-radius: 5px 5px;
}
.footer{
  background-color: rgba(0, 0, 0, 0.281);
}
.topic{
  color: #fff;
  padding: 10px;
  font-size: 1.7rem;
  border-bottom: 1px solid rgb(255, 255, 255);
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.links{
  padding: 40px;
  padding-top: 20px;
  text-decoration: none;
  line-height: 30px;
  color: #fff;
  font-size: 1rem;
}
.links:hover{
  color: red;
}
.ad{
  background-color: rgba(0, 0, 0, 0.281);
}
.ad a{
  color: #fff;
  text-decoration: none;
  font-size: 2.4rem;
  animation-name: ad;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.ad a:hover{
  border-bottom: 1px solid red;
  border-radius: 20px 20px;
}
@keyframes ad{
  0%{color: white;}
  25%{color: #0ff;}
  50%{color: #f0f;}
  75%{color: lime;}
  100%{color: white;}
}
@media screen and (max-width: 800px){
  .ad a{
    font-size: 1.7rem;
  }
}
@media screen and (max-width: 600px){
  .ad a{
    font-size: 1.3rem;
  }
}
.socal{
  padding: 50px;
  border-top: 1px solid white;
}
.socal img{
  border: 10px solid rgba(255, 0, 0, 0);
}
@media screen and (max-width: 400px){
  .socal img{
    width: 50px;
    height: 50px;
  }
}


@import url(https://fonts.googleapis.com/css?family=Raleway);

*, *:before, *:after{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing:border-box;
  box-sizing: border-box;
}



.main-title{
  color: #2d2d2d;
  text-align: center;
  text-transform: capitalize;
}

.container{
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 640px){
  .container{
    width: 100%;

  }
}

@media screen and (min-width: 900px){
    .container{
        width: 100%;
        height: 100%;
      }
}

.container .title{
  color: #1a1a1a;
  text-align: center;
  margin-bottom: 10px;
}

.content {
  position: relative;
  width: 100%;
  margin: auto;
  overflow: hidden;
  height: 100%;
}

.content .content-overlay {
  background: rgba(0,0,0,0.7);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay{
  opacity: 1;
}

.content-image{
  width: 100%;
}

.content-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.content:hover .content-details{
  top: 50%;
  left: 50%;
  opacity: 1;
}

.content-details h3{
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.content-details p{
  color: #fff;
  font-size: 0.8em;
}

.fadeIn-bottom{
  top: 80%;
}

.fadeIn-top{
  top: 20%;
}

.fadeIn-left{
  left: 20%;
}

.fadeIn-right{
  left: 80%;
}