.splide {
    padding: 0em; /*3em*/
}
.res{
    width: 100%;
    height: 300px;
    
}
@media screen and (max-width: 370px) {
    .res{
       width: 100%;
       height: 37vh;
    }
    }
@media screen and (min-width: 600px) {
    .res{
       width: 100%;
       height: 400px;
    }
    }
    @media screen and (min-width: 850px) {
        .res{
           width: 100%;
           height: 500px;
        }
        }
        @media screen and (min-width: 1000px) {
            .res{
               width: 100%;
               height: 600px;
            }
            }
            @media screen and (min-width: 1200px) {
                .res{
                   width: 100%;
                   height: 700px;
                }
                }
