.navbar{
    background-color: rgba(255, 255, 255, 0);
    padding: 9px 20px 9px;
    font-weight: 700;
    z-index: 1;
    position: absolute;
    top: 0px;
    color: azure;
    word-spacing: 3rem;
}
.navbar a{
    color: rgb(255, 0, 51);
    word-spacing: 3rem;
    border-spacing: 3rem;
}
.navbar a:hover{
    border-bottom: 3px solid rgb(163, 0, 0);
    color: rgb(163, 0, 0);
   
    
}
.text{
    font-size: 1.2rem;
    color: white;
    text-decoration: none;
    margin: 10px;
}
@media screen and (min-width: 700px){
    .text{
        font-size:1.6rem;
    }
}
@media screen and (min-width: 900px){
    .text{
        font-size:2rem;
        position: relative;
        left: 60px;
        top: 20px;
    }
}